import { getEmailBody } from "../email-helper/get-email-data";

export async function processFull() {
  return new Promise(async (resolve, reject) => {
    try {
      const emailBody = await getEmailBody();
   
      
      const response = await fetch("https://mbcm-dev-fastapi2.icybeach-c4dd47be.australiasoutheast.azurecontainerapps.io/api/v1/process-full", {
        method: "POST",
        headers: {
          accept: "application/json",
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          email_content: emailBody,
          outlook_owner: Office.context.mailbox.userProfile.emailAddress,
          mailbox: "inbox",
        }),
      });

      if (!response.ok) {
        return reject(new Error(`HTTP error! Status: ${response.status}`));
      }

      const data = await response.json();
      resolve(data);
    } catch (error) {
      reject(error);
    }
  });
}




