import React, { useState, useEffect } from "react";
import { Select } from "@fluentui/react-components";
import "../../../styles/draft-response.css";
import { draftResponse } from "../../../api-services/draft-response";
import { config } from "../../../config";
import { getEmailBody } from "../../../email-helper/get-email-data";
import { useLoading } from "../index";
import { copyToClipboard } from "../../../utilities/copy";

const DraftResponse = ({ registerReset }) => {
  const [draftData, setDraftData] = useState(null);
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState(null);
  const { updateLoadingState } = useLoading();
  const [form, setForm] = useState({
    length: "Brief",
    purpose: "Acknowledge"
  });

  const handleDraftEmail = async () => {
    try {
      setIsLoading(true);
      setError(null);
      updateLoadingState(true);
      
      const emailBody = await getEmailBody();
      const requestBody = {
        "email_content": emailBody,
        "length": form.length,
        "purpose": form.purpose,
        "tone": "Professional",
        "custom_tone_name": "",
        "custom_tone_instructions": "",
        "custom_purpose_name": "",
        "custom_purpose_instructions": "",
        "custom_length_name": "",
        "custom_length_instructions": "",
        "tenant_id": ""
      }
      const result = await draftResponse(requestBody);
      setDraftData(result);
    } catch (err) {
      setError(err.message);
    } finally {
      setIsLoading(false);
      updateLoadingState(false);
    }
  };

  const handleCopyToClipboard = () => {
    if (draftData) {
      copyToClipboard(draftData.subject + "\n" + draftData.greeting + "\n" + draftData.body + "\n" + draftData.closing);
    }
  };

  // function to update form fields
  const handleChange = (name, value) => {
    setForm((prevForm) => ({
      ...prevForm,
      [name]: value,
    }));
  };

  return (
    <div className="draft-response-container">
      {!isLoading && !error && !draftData && (
        <div className="draft-response-select">
          <div style={{ flex: 1 }}>
            <label>Response Type</label>
            <Select value={form.purpose} onChange={(e) => handleChange("purpose", e.target.value)}>
              {config.RESPONSE_TYPES.map((type) => (
                <option key={type.key} value={type.value}>{type.value}</option>
              ))}
            </Select>
          </div>
          <div style={{ flex: 1 }}>
            <label>Length of Response</label>
            <Select value={form.length} onChange={(e) => handleChange("length", e.target.value)}>
              {config.RESPONSE_LENGTHS.map((length) => (
                <option key={length.key} value={length.value}>{length.value}</option>
              ))}
            </Select>
          </div>
        </div>
      )}

      {!isLoading && !error && !draftData && (
        <div style={{ 
          display: 'flex', 
          alignItems: 'center', 
          justifyContent: 'center', 
          width: '100%', 
          height: '200px' 
        }}>
          <button
            style={{
              background: '#2d2d73',
              color: 'white',
              border: 'none',
              borderRadius: '6px',
              padding: '12px 24px',
              fontSize: '16px',
              fontWeight: '500',
              cursor: 'pointer'
            }}
            onClick={handleDraftEmail}
          >
            Draft Response
          </button>
        </div>
      )}

      {isLoading && (
        <div style={{ 
          display: 'flex', 
          justifyContent: 'center', 
          alignItems: 'center', 
          height: '200px', 
          fontSize: '18px', 
          color: '#666' 
        }}>
          Processing draft...
        </div>
      )}

      {!isLoading && !error && draftData && (
        <>
          <div className="draft-response-content">
            {draftData.subject && <h4 style={{ margin: "0 0 16px 0", color: "#2d2d73" }}>Subject: {draftData.subject}</h4>}
            {draftData.greeting && <p style={{ margin: "0 0 12px 0" }}>{draftData.greeting}</p>}
            {draftData.body && <p style={{ margin: "0 0 12px 0" }}>
              {draftData.body}
            </p>}
            <p style={{ margin: "0 0 12px 0" }}>
              I will be reviewing the scope documentation and will provide you with a comprehensive response within the next
              24 hours. Please let me know if you need any additional information or have any urgent concerns.
            </p>
            {draftData.closing && <p style={{ margin: "0 0 12px 0" }}>
              {draftData.closing}
            </p>}
          </div>
          <div className="draft-response-select-btn-copy">
            <button onClick={handleCopyToClipboard}>Copy to Clipboard</button>
          </div>
        </>
      )}

      {!isLoading && !draftData && error && <div style={{ color: "red" }}>Error: {error}</div>}
    </div>
  );
};

export default DraftResponse;
