
export async function getEmailBody() {
  return new Promise((resolve, reject) => {
    try {
      // Validate Office context first
      if (!Office.context?.mailbox?.item) {
        reject(new Error('Office context or mailbox item not available'));
        return;
      }

      // Get email body
      Office.context.mailbox.item.body.getAsync(
        Office.CoercionType.Text, // ensures plain text
        (result) => {
          if (result.status === Office.AsyncResultStatus.Succeeded) {
            try {
              // Validate that item still exists and is accessible
              if (!Office.context?.mailbox?.item) {
                reject(new Error('Email item no longer available'));
                return;
              }

              // Get email subject
              const subject = Office.context.mailbox.item.subject || '';

              // Get sender information
              const from = Office.context.mailbox.item.from?.emailAddress || '';

              // Get recipient information
              const to = Office.context.mailbox.item.to?.[0]?.emailAddress || '';

              // Get email date
              const date = Office.context.mailbox.item.dateTimeCreated?.toISOString() || new Date().toISOString();

              // Format the email content in the required structure
              const formattedEmailContent = `Subject: ${subject}
From: ${from}
To: ${to}
Date: ${new Date(date).toLocaleString()}

${result.value}`;

              resolve(formattedEmailContent);
            } catch (err) {
              reject(new Error(`Failed to format email content: ${err.message}`));
            }
          } else {
            reject(new Error(result.error.message));
          }
        }
      );
    } catch (err) {
      reject(new Error(`Office context error: ${err.message}`));
    }
  });
}











export function registerItemChangedHandler() {
  Office.context.mailbox.addHandlerAsync(
    Office.EventType.ItemChanged,
    async () => {
      console.log("📩 Item changed, fetching new body...");
    //  const emailData = await getEmailBody();
      window.location.reload();
    }
  );
}
