import  React, { useEffect} from "react";
import { makeStyles, tokens, Tab, TabList } from "@fluentui/react-components";
import ProcessEmail from "./process-email";
import DraftResponse from "./draft-response";

// Create a context for active tab and loading state
const LoadingContext = React.createContext();

// Custom hook to use loading context
export const useLoading = () => {
  const context = React.useContext(LoadingContext);
  if (!context) {
    throw new Error('useLoading must be used within a LoadingProvider');
  }
  return context;
};

const useStyles = makeStyles({
  root: {
    alignItems: "flex-start",
    display: "flex",
    flexDirection: "column",
    justifyContent: "flex-start",
    padding: "0",
    rowGap: "0",
  },
  panels: {
    padding: "0 10px",
    "& th": {
      textAlign: "left",
      padding: "0 30px 0 0",
    },
  },
  propsTable: {
    "& td:first-child": {
      fontWeight: tokens.fontWeightSemibold,
    },
    "& td": {
      padding: "0 30px 0 0",
    },
  },
  tabList: {
    "& button": {
      backgroundColor: "white",
      color: "#2d2d73",
      border: "2px solid #2d2d73",
      borderBottom: "none",
      borderRadius: "6px 6px 0 0",
      marginRight: "2px",
      padding: "8px 16px",
      fontWeight: "500",
      fontSize: "13px",
      transition: "all 0.2s ease",
    },
    "& button:last-child": {
      marginRight: "0",
    },
    "& button:disabled": {
      backgroundColor: "#f5f5f5",
      color: "#999",
      borderColor: "#ddd",
      cursor: "not-allowed",
      opacity: 0.6,
    },
  },
});

export const HomeTabs = () => {
  const styles = useStyles();
  const [selectedValue, setSelectedValue] = React.useState("ProcessEmail");
  const [isAnyTabLoading, setIsAnyTabLoading] = React.useState(false);


  const onTabSelect = (event, data) => {
    // Prevent tab switching if any tab is loading
    if (isAnyTabLoading) {
      return;
    }
    setSelectedValue(data.value);
  };

  // Function to update loading state from child components
  const updateLoadingState = (isLoading) => {
    setIsAnyTabLoading(isLoading);
  };

  return (
    <LoadingContext.Provider value={{ 
      activeTab: selectedValue, 
      updateLoadingState
    }}>
      <div
        className={styles.root}
        style={{
          flex: 1,
          display: "flex",
          flexDirection: "column",
        }}
      >
        <TabList
          selectedValue={selectedValue}
          onTabSelect={onTabSelect}
          className={styles.tabList}
          style={{
            justifyContent: "center",
            width: "100%",
          }}
        >
          <Tab 
            id="ProcessEmail" 
            value="ProcessEmail"
            disabled={isAnyTabLoading}
          >
            Process Email
          </Tab>
          <Tab 
            id="DraftResponse" 
            value="DraftResponse"
            disabled={isAnyTabLoading}
          >
            Draft Response
          </Tab>
        </TabList>
        <div
          className={styles.panels}
          style={{
            height: "calc(100vh - 45px - 2rem - 2rem)",
            overflowY: "auto",
          }}
        >
          {selectedValue === "ProcessEmail" && <ProcessEmail  />}
          {selectedValue === "DraftResponse" && <DraftResponse />}
        </div>
      </div>
    </LoadingContext.Provider>
  );
};
