import React, { useState, useEffect } from "react";
import ActionItemCard from "../../../components/action-item-card";
import { processFull } from "../../../api-services/process-email";
import { useLoading } from "../index";
import "../../../styles/action-items.css";
import "../../../styles/home.css";

const ProcessEmail = () => {
  const [emailData, setEmailData] = useState(null);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState(null);
  const { updateLoadingState } = useLoading();

  // Process email when component mounts
  useEffect(() => {
    handleProcessEmail();
  }, []);

  // Update parent tabs when loading state changes
  useEffect(() => {
    updateLoadingState(isLoading);
  }, [isLoading, updateLoadingState]);

  const handleProcessEmail = async () => {
    try {
      setIsLoading(true);
      setError(null);
      const result = await processFull();
      setEmailData(result);
    } catch (err) {
      setError(err.message);
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <div className="action-items-container">
      <div className="home-header">
        <div className="header-content">
          {isLoading
            ? "Loading..."
            : error
              ? "Error"
              : emailData?.classification || "Process Email"}
        </div>
      </div>

      {isLoading && (
        <div style={{
          display: 'flex',
          justifyContent: 'center',
          alignItems: 'center',
          height: '200px',
          fontSize: '18px',
          color: '#666'
        }}>
          Processing email...
        </div>
      )}

      {error && (
        <div style={{
          display: 'flex',
          justifyContent: 'center',
          alignItems: 'center',
          height: '200px',
          fontSize: '18px',
          color: 'red'
        }}>
          Error: {error}
        </div>
      )}

      {!isLoading && !error && emailData && (
        <>
          <div className="email-summary">
            <h4>Email Summary</h4>
            <div className="email-summary-content">
              <div className="summary-text">
                {emailData.summary?.summary || "N/A"}
                {emailData.summary?.key_points?.length > 0 && (
                  <div>
                    <strong>Key Points:</strong>
                    <ul>
                      {emailData.summary.key_points.map((point, i) => (
                        <li key={i}>{point}</li>
                      ))}
                    </ul>
                  </div>
                )}
              </div>
            </div>
          </div>

          {emailData.oc_details?.length > 0 && (
            <div className="owners-corporations">
              <h4>Owners Corporations</h4>
              <ul>
                {emailData.oc_details.map((oc, i) => (
                  <li key={i}>
                    <p>OC {oc.BCSP_NO}</p>
                    <p>
                      Lot {oc.SubNo} {oc.BCSPStreet} {oc.BCSPSuburb} {oc.BCSPState}{" "}
                      {oc.BCSPPostcode} {oc.UNIT_NO}
                    </p>
                  </li>
                ))}
              </ul>
            </div>
          )}

          {emailData.action_items?.length > 0 && (
            <div className="action-items-section">
              <h4>Action Items</h4>
              <div className="action-items">
                <ActionItemCard items={emailData.action_items} />
              </div>
            </div>
          )}

          <div className="create-action-items-btn">
            <button onClick={handleProcessEmail} disabled={isLoading}>
              Process Email Again
            </button>
          </div>
        </>
      )}
    </div>
  );
};

export default ProcessEmail;
