import React from "react";
import { Checkbox } from "@fluentui/react-components";
import "../styles/action-item-card.css";

const ActionItemCard = ({items}) => {
  // Function to format property keys for display
  const formatPropertyKey = (key) => {
    return key
      .replace(/_/g, ' ') // Replace underscores with spaces
      .replace(/\b\w/g, l => l.toUpperCase()); // Capitalize first letter of each word
  };

  // Function to render item properties
  const renderItemProperties = (item) => {
    return Object.entries(item).map(([key, value]) => {
      // Skip rendering the id field since we already show it in the title
      if (key === 'id') return null;
      
      const formattedKey = formatPropertyKey(key);
      
      return (
        <p key={key}>
          <b>{formattedKey}:</b> {value}
        </p>
      );
    });
  };

  return (
    <>
      {items.map((item, index) => (
        <div key={index} className="action-item-card">
          <Checkbox />
          <div className="action-item-card-content">
            <h4>Action Item {item.id || index + 1}</h4>
            {renderItemProperties(item)}
          </div>
        </div>
      ))}
    </>
  );
};

export default ActionItemCard;
