r\n  */\r\n.create-action-items-btn {\r\n  position: fixed;\r\n  bottom: 15px;\r\n  left: 50%;\r\n  transform: translateX(-50%);\r\n  border-radius: 6px;\r\n  z-index: 1000;\r\n}\r\n/* Button styling for Copy button */\r\n.create-action-items-btn button {\r\n  background: #2d2d73;\r\n  color: white;\r\n  border: none;\r\n  border-radius: 6px;\r\n  padding: 10px 16px;\r\n  font-weight: 500;\r\n  cursor: pointer;\r\n}\r\n\r\n.action-items-container {\r\n  display: flex;\r\n  flex-direction: column;\r\n  height: 100%; /* fill parent panel */\r\n  min-height: 0; /* Allow flex shrinking */\r\n  overflow-y: auto !important; /* scroll if content overflows */\r\n  width: 100%;\r\n}\r\n\r\n.action-items-section {\r\n  margin-bottom: 20px;\r\n  width: 100%;\r\n  flex: 1; /* Take remaining space */\r\n  display: flex;\r\n  flex-direction: column;\r\n}\r\n\r\n.action-items-section h4 {\r\n  margin-bottom: 10px;\r\n  margin-top: 0;\r\n  color: #2d2d73;\r\n  font-size: 16px;\r\n  font-weight: 600;\r\n  display: flex;\r\n  align-items: center;\r\n}\r\n\r\n.action-items-section h4::before {\r\n  content: \"📋\";\r\n  margin-right: 8px;\r\n  font-size: 18px;\r\n}\r\n\r\n.action-items {\r\n  display: flex;\r\n  flex-direction: column;\r\n  max-height: calc(100vh - 300px); /* Dynamic height based on viewport */\r\n  min-height: 200px; /* Minimum height for small screens */\r\n  overflow-y: auto;\r\n  overflow-x: hidden;\r\n  margin-right: 5px;\r\n}\r\n\r\n/* Dynamic UI Elements */\r\n.read-more-btn, .view-all-btn, .expand-btn, .collapse-btn {\r\n  background: #2d2d73;\r\n  color: white;\r\n  border: none;\r\n  border-radius: 4px;\r\n  padding: 6px 12px;\r\n  font-size: 12px;\r\n  font-weight: 500;\r\n  cursor: pointer;\r\n  margin-left: 8px;\r\n  transition: background-color 0.2s ease;\r\n}\r\n\r\n.read-more-btn:hover, .view-all-btn:hover, .expand-btn:hover, .collapse-btn:hover {\r\n  background: #1a1a4a;\r\n}\r\n\r\n.collapse-btn {\r\n  background: #666;\r\n  margin-top: 10px;\r\n  width: 100%;\r\n  padding: 8px 12px;\r\n}\r\n\r\n.collapse-btn:hover {\r\n  background: #555;\r\n}\r\n\r\n/* Summary Preview/Full */\r\n.summary-preview {\r\n  line-height: 1.5;\r\n  color: #333;\r\n}\r\n\r\n.summary-full {\r\n  line-height: 1.5;\r\n  color: #333;\r\n}\r\n\r\n.key-points-section {\r\n  margin-top: 15px;\r\n}\r\n\r\n.key-points-section ul {\r\n  margin: 8px 0;\r\n  padding-left: 20px;\r\n}\r\n\r\n.key-points-section li {\r\n  margin-bottom: 5px;\r\n}\r\n\r\n/* Owners Corporations Preview/Full */\r\n.oc-preview {\r\n  display: flex;\r\n  justify-content: space-between;\r\n  align-items: center;\r\n  padding: 12px;\r\n  border: 1px solid #6387c5;\r\n  border-radius: 6px;\r\n  background: white;\r\n}\r\n\r\n.oc-count {\r\n  font-weight: 500;\r\n  color: #2d2d73;\r\n}\r\n\r\n.oc-full ul {\r\n  margin: 0;\r\n  list-style-type: none;\r\n  line-height: 1.5;\r\n  max-height: none;\r\n  overflow-y: visible;\r\n  padding: 12px;\r\n  border: 1px solid #6387c5;\r\n  border-radius: 6px;\r\n  background: white;\r\n}\r\n\r\n/* Action Items Preview/Full */\r\n.action-items-preview {\r\n  border: 1px solid #6387c5;\r\n  border-radius: 6px;\r\n  background: white;\r\n  padding: 12px;\r\n}\r\n\r\n.action-item-preview {\r\n  padding: 8px 0;\r\n  border-bottom: 1px solid #eee;\r\n}\r\n\r\n.action-item-preview:last-child {\r\n  border-bottom: none;\r\n}\r\n\r\n.action-item-title {\r\n  font-weight: 500;\r\n  color: #2d2d73;\r\n}\r\n\r\n.view-all-btn {\r\n  margin-top: 10px;\r\n  width: 100%;\r\n  padding: 8px 12px;\r\n}\r\n\r\n.action-items-full {\r\n  border: 1px solid #6387c5;\r\n  border-radius: 6px;\r\n  background: white;\r\n  padding: 12px;\r\n}\r\n\r\n/* Responsive adjustments */\r\n@media (max-width: 768px) {\r\n  .read-more-btn, .view-all-btn, .expand-btn, .collapse-btn {\r\n    font-size: 11px;\r\n    padding: 5px 10px;\r\n  }\r\n  \r\n  .oc-preview {\r\n    flex-direction: column;\r\n    gap: 10px;\r\n    align-items: flex-start;\r\n  }\r\n}\r\n"],"sourceRoot":""