import { getEmailBody } from "../email-helper/get-email-data";

export async function draftResponse(requestBody) {
  return new Promise(async (resolve, reject) => {
    try {

      const response = await fetch("https://mbcm-dev-fastapi2.icybeach-c4dd47be.australiasoutheast.azurecontainerapps.io/api/v1/draft-response", {
        method: "POST",
        headers: {
          accept: "application/json",
          "Content-Type": "application/json",
        },
        body: JSON.stringify(requestBody),
      });

      if (!response.ok) {
        return reject(new Error(`HTTP error! Status: ${response.status}`));
      }

      const data = await response.json();
      resolve(data);
    } catch (error) {
      reject(error);
    }
  });
}
